@import url('https://fonts.googleapis.com/css2?family=Chakra+Petch:ital,wght@0,400;0,700;1,400;1,700&family=Cousine:ital,wght@0,400;0,700;1,400;1,700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

body {
    background-color: dimgrey;
    background-color: rgb(80, 80, 80);
    background-color: #b8b8b8;
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
}

.content__section h1 {
    color: yellow;
    font-size: 14rem;
}

.container {
    width: 100%;
    height: 100vh;
    overflow-y: scroll;
    scroll-snap-type: y mandatory;
}

.content__section {
    height: 100vh;
    scroll-snap-align: start;
    scroll-snap-stop: always;
}

.content__section:nth-of-type(1) {
    background-image: linear-gradient(to left bottom, #370d4a, #2f124b, #27154b, #1f184a, #171a49, #181e55, #192261, #19266e, #2f2a8c, #4d2aa8, #6f23c1, #9600d8);
    overflow: hidden;
    position: relative;
}

.content__section:nth-of-type(2) {
    background-color: #171a49;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content__section:nth-of-type(3) {
    background-image: linear-gradient(to left bottom, #571dfb, #3427d7, #1528b2, #00258c, #001f67, #011f65, #032064, #042062, #212683, #4428a2, #6b22bf, #9600d8);
    display: flex;
    justify-content: center;
    align-items: center;
}

#cursor {
    position: absolute;
    width: 55px;
    height: 55px;
    border-radius: 50%;
    background-color: rgb(255, 0, 190);
    pointer-events: none;
    mix-blend-mode: difference;
    z-index: 100;
}


/* RZEŹBA - PARALAXA */

.sculpture {
    background-image: url('/public/photo2.png');
    height: 100vh;
    margin-right: 20%;
    width: auto;
    background-position: top;
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
    overflow: hidden;
}

.parallax-container,
.parallax-image {
    overflow: hidden;
}


/*  NAWIGACJA  */

.sections__nav {
    transform: rotate(270deg);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
    margin-right: auto;
    z-index: 99;
    margin-right: auto;
    width: fit-content;
    width: 4.75%;
    width: 75px;
    display: none;
    position: absolute;
    top: 32.5%;
    opacity: 0;
    animation: fadeIn 0.15s ease-in-out forwards;
    color: #fff;
}

.sections__nav.nav__open {
    display: flex;
    opacity: 1;
}

.sections__nav li {
    list-style: none;
    font-family: 'Cousine', monospace;
    font-size: 1.25rem;
    margin: 0 0.75rem;
    cursor: pointer;
    transition: .2s;
    user-select: none;
}

.sections__nav li:hover {
    color: aqua;
    /* PASEK PRZEZ ŚRODEK */
}


/*  HAMBURGER MENU  */

.navigation {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    margin-left: 2.25rem;
    margin-top: 2rem;
}

label {
    display: flex;
    flex-direction: column;
    width: 70px;
    cursor: pointer;
}

label span {
    background: #fff;
    border-radius: 10px;
    height: 7px;
    margin: 7px 0;
    transition: .4s cubic-bezier(0.68, -0.6, 0.32, 1.6);
}

span:nth-of-type(1) {
    width: 50%;
}

span:nth-of-type(2) {
    width: 100%;
}

span:nth-of-type(3) {
    width: 75%;
}

input[type="checkbox"] {
    display: none;
}

input[type="checkbox"]:checked~span:nth-of-type(1) {
    transform-origin: bottom;
    transform: rotatez(45deg) translate(8px, 0px)
}

input[type="checkbox"]:checked~span:nth-of-type(2) {
    transform-origin: top;
    transform: rotatez(-45deg)
}

input[type="checkbox"]:checked~span:nth-of-type(3) {
    transform-origin: bottom;
    width: 50%;
    transform: translate(30px, -11px) rotatez(45deg);
}

.app,
.todos {
    margin-top: 10vh;
}

.tasks {
    list-style: none;
}

.task {
    display: flex;
    justify-content: center;
    align-items: center;
}

.name {
    font-family: 'Roboto', sans-serif;
    font-family: 'Cousine', monospace;
    font-family: 'Chakra Petch', sans-serif;
    font-size: 9.25rem;
    font-weight: bold;
    font-style: italic;
    color: white;
    position: absolute;
    top: 14%;
    right: 13.5%;
    user-select: none;
    opacity: 0;
    animation: fadeIn 0.5s ease-in-out forwards;
    overflow: none;
}

.name span {
    font-family: 'Cousine', monospace;
    font-family: 'Chakra Petch', sans-serif;
    margin-left: 35%;
    opacity: 0;
    animation: fadeIn 0.5s ease-in-out forwards;
}

.job__title {
    font-size: 2.2rem;
    top: 70%;
    right: 6%;
    font-family: 'Chakra Petch', sans-serif;
    font-family: 'Cousine', monospace;
    color: #fff;
    position: absolute;
    font-weight: bold;
    font-style: italic;
    opacity: 0;
    animation: fadeIn 1.35s ease-in-out forwards;
}

.job__title.visible {
    opacity: 1;
}


/* ANIMACJA POJAWIANIA SIĘ IMIENIA & NAZWISKA */

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.name:nth-child(1) {
    animation-delay: 0.15s;
}

.name:nth-child(2) {
    animation-delay: 0.3s;
}

.name:nth-child(3) {
    animation-delay: 0.45s;
}

.name:nth-child(4) {
    animation-delay: 0.6s;
}

.name:nth-child(5) {
    animation-delay: 0.75s;
}

.name:nth-child(6) {
    animation-delay: 0.9s;
}

.name span:nth-child(1) {
    animation-delay: 0.15s;
}

.name span:nth-child(2) {
    animation-delay: 0.3s;
}

.name span:nth-child(3) {
    animation-delay: 0.45s;
}

.name span:nth-child(4) {
    animation-delay: 0.6s;
}

.name span:nth-child(5) {
    animation-delay: 0.75s;
}

.name span:nth-child(6) {
    animation-delay: 0.9s;
}